<template>
  <div class="Survey">
    <div
      class="corner">
      <svg xmlns="http://www.w3.org/2000/svg" width="60" height="60" viewBox="0 0 60 60" fill="none">
        <path d="M60 60L60 8C60 3.58172 56.4183 4.27116e-08 52 9.5399e-08L-3.8147e-06 7.15493e-07L60 60Z" :fill="survey.color"/>
      </svg>
    </div>
    <div
      class="content">
      <div
        v-if="survey.type"
        class="tag tag-type">{{ surveyType }}</div>
      <h1 class="name">{{ survey.name | truncate(40) }}</h1>
      <h3 class="description">{{ survey.description | truncate(70) }}</h3>
    </div>

    <img
      v-if="survey.cover_url"
      :src="imagePath"
      alt="image"
      class="image"
    >
  </div>
</template>

<script>

export default {
  name: 'Survey',

  filters: {
    truncate(str, n = 80) {
      if (str?.length > n) {
        return `${str.slice(0, n - 3)}...`;
      }

      return str;
    },
  },

  props: {
    survey: {
      type: Object,
      default: () => ({}),
    },
  },

  computed: {
    imagePath() {
      return this.survey.cover_url ? `${process.env.VUE_APP_API}${this.survey.cover_url}` : require('@/assets/trianglify.svg');
    },

    surveyType() {
      return this.$store.getters['surveys/getTypeByKey'](this.survey.type)?.value;
    },
  },
};
</script>

