<template>
  <div class="Surveys">
    <div class="header">
      <h1 class="title">
        Публичные опросы
        <div class="small">
          Участвуйте в опросах и влияйте на общественную и политическую жизнь региона.
        </div>
      </h1>
    </div>

    <div
      v-loading="isGettingSurveys"
      class="container"
    >
      <router-link
        v-for="survey in surveys"
        :key="survey.id"
        :to="{
          name: 'SurveyPassing',
          params: { id: survey.id }
        }"
        tag="div"
      >
        <Survey :survey="survey" />
      </router-link>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import Survey from '../components/Survey.vue';

export default {
  name: 'Surveys',

  metaInfo: {
    title: 'Опросы',
  },

  components: {
    Survey,
  },

  data() {
    return {
      isGettingSurveys: true,
      surveys: [],
    };
  },

  computed: {
    ...mapState('auth', ['current_user']),
  },

  async mounted() {
    if (this.current_user) {
      await this.getSurveys({ url: '/user/surveys' });
    } else {
      await this.getSurveys({ url: '/surveys' });
    }
  },

  methods: {
    async getSurveys({ url }) {
      try {
        const { data } = await this.$http.get(url);
        this.surveys = data.filter(survey => survey.published
          && !survey.link_only
          && !survey.archived_at);
        // Быстрое решение. Сортируем опросы по дате и вначале показываем самые новые.
        this.surveys.sort((a, b) => new Date(b.created_at) - new Date(a.created_at));
      } catch (err) {
        console.error('Surveys / getSurveys', err);
      } finally {
        this.isGettingSurveys = false;
      }
    },
  },
};
</script>

